class Nav extends React.Component {


  constructor(props) {
    super(props)
  }

  renderNavLink(name, content) {

    return (

      <button
        onClick={() => this.props.onClick(content)}>
          {name}
      </button>
    )
  }

  renderMedia(img, alt, href) {
    return (

      <a href={href} target="_blank" rel="noopener noreferrer">
        <img src={img} alt={alt} />
      </a>



    )
  }

  render(){
    return (
      <div className="nav">
        <div
          className="wordmark">
          <h1
          onClick = {() => this.props.onClick(<Landing />)}
          >
            Quinn Voronin
          </h1>
        </div>
        <hr />
        <div className="navLinks">
          {this.renderNavLink("Skills", <Skills />)}
          {this.renderNavLink("About", <About />)}
          {this.renderNavLink("Work", <Work />)}
        </div>

        <div className="bottom">
          <div className="media">
            {this.renderMedia("GitHub-Mark-Light-64px.png", "github", "https://github.com/buinton")}
            {this.renderMedia("instagram-light-64px.png", "instagram", "https://www.instagram.com/quinnvoronin/")}
          </div>
          <div className="contact-me">
            contact me
          </div>

        </div>
      </div>
    )
  }
}


class Content extends React.Component {


  // Responsible for rendering the main content field.
  constructor(props) {
    super(props);

  }

  render() {
    return (
      <div className="content">
        {this.props.currentMedia}
      </div>
    )
  }
}


class App extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      currentMedia: <Landing />,
    };
  }

  handleMediaChange = (newMedia) => {
    this.setState({
      currentMedia: newMedia,
    });
  }

  render() {
    return (

      <div className="app">
        <div className="nav">
          <Nav onClick={this.handleMediaChange}/>
        </div>
        <Content
          currentMedia={this.state.currentMedia} />
      </div>
    );
  }
}

export default App;
